        top: -20px;
    }
    .intro-container-div{
        height: 700px;
        position: relative;
        margin: -32rem auto;
        padding-bottom: 1rem;
        text-align: center;
        padding: 3rem 1.5rem;
        background-color: white;
        border-radius: 20px;
        box-shadow: 0 0 0 0 rgba(233,240,243,0.5), 0 0 0 0 #E6ECF8;
    }
    .intro-text {
